/* fuentes */

:root {
  --primary-font: 'Goudy Bookletter 1911', 'Times New Roman', serif;
  --secundary-font: 'Cinzel', serif;
}

/* colores */

:root {
  --color-primario: #4B0082;
  /* TITULOS*/
  --color-secundario: #f5f5f5d8;
  /* PARRAFOS */
  --color-terciario: #abff03;

}


/* aplicacion en estilos */
h1,
h2,
h3 {
  color: var(--color-primario);
  font-family: var(--primary-font);
}

h4,
h5,
h6 {
  color: var(--color-secundario);
  font-family: var(--secundary-font);
}

a,
span,
li,
ul {
  color: var(--color-secundario);
  text-decoration: overline;
  font-family: var(--primary-font);

}

p {
  color: var(--color-terciario);
}

body,
html {
  background-color: black;
  z-index: 5;
}


