/* gpt solution */
.container-primary-card-service {
  position: relative;
}

.card-services {
  position: relative;
  overflow: hidden;
}

.service-description-price {
  position: absolute;
  padding-top: 25%;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 10px;
  background-color: rgba(75, 0, 130, 0.7);
  /* Morado transparentoso */
  color: #ffffff;
  transition: transform 0.5s ease, opacity 0.5s ease;
  transform: translateY(100%);
  opacity: 0;
}

.hidden {
  opacity: 0;
}

.visible {
  transform: translateY(0%);
  opacity: 1;
}


